import { Component, OnDestroy, OnInit } from '@angular/core';
import { Event } from '../shared/models/event';
import { LatLng, LatLngBounds } from 'leaflet';
import { EventService } from '../shared/services/event.service';
import { Events } from '../shared/models/event-list';
import { Dates } from '../shared/models/date-list';
import { FormControl } from '@angular/forms';
import { EventsApiService } from '../shared/services/events-api.service';
import { UserService } from '../shared/services/user.service';
import { User } from '../shared/models/user.model';
import { Subscription } from 'rxjs';


@Component({
  selector: 'app-main',
  templateUrl: './main.component.html',
  styleUrls: ['./main.component.css']
})
export class MainComponent implements OnInit, OnDestroy {

  events: Array<Event>;
  eventsOnMap: Array<Event>;
  eventsFromHQ: Array<any>;
  eventToDisplay: Event;
  eventToZoom: LatLng;
  typeEvents = Events;
  dates = Dates;
  filtersType: FormControl;
  filtersDate: FormControl;
  currentDate: Date;
  mapBounds: LatLngBounds;
  user: User;
  public subscription: Subscription;

  constructor(
    private userService: UserService,
    private eventService: EventService,
    private eventsApiService: EventsApiService
  ) {
    this.events = new Array<Event>();
    this.eventsOnMap = new Array<Event>();
    this.eventsFromHQ = new Array<any>();
    this.filtersType = new FormControl();
    this.filtersDate = new FormControl();
    this.currentDate = new Date();
  }

  ngOnInit(): void {
    this.eventService.events.subscribe((events: Array<Event>) => {
      this.events = events
    });
    this.subscription = this.userService.currentUser.subscribe((user: User) => {
      if (user) {
        this.user = new User(user._id, user.email, user.name, user.profile_type, user.amis);
      }
    });
    //this.eventsApiService.signRequestForMusicStory();
    //this.predictHQ();
  }

  predictHQ() {
    for (let i = 0; i < 1000; i += 50) {
      this.eventsApiService.getEventsFromPredictHQ(i).subscribe((res) => {
        let names = new Array<string>();
        for (let e of this.events) {
          names.push(e.name);
        }
        this.eventsFromHQ = res.results;
        for (let event of this.eventsFromHQ) {
          if (names.indexOf(event.title) === -1) {
            const ev = new Event();
            ev.scope = "public";
            ev.name = event.title;
            ev.dateDebut = event.start;
            ev.dateFin = event.end;
            if (ev.name.startsWith("LE LIDO")) {
              ev.type = 'cabaret';
              ev.createur = "Le Lido";
            } else if (ev.name.startsWith("THE WORLD OF BANKSY")) {
              ev.type = 'expos';
            } else if (event.labels.indexOf("comedy") !== -1) {
              ev.type = 'théâtre';
            } else {
              ev.type = event.category;
            }
            ev.description = event.description;
            if (event.entities[0]) {
              ev.lieu = event.entities[0].formatted_address
            }
            ev.latitude = event.location[1];
            ev.longitude = event.location[0];
            console.log(ev);
            //this.eventService.createEvent(ev);
          }
        }
      });
    }
  }

  displayEvent(event: Event) {
    this.eventToDisplay = event;
  }

  ngOnDestroy(): void {
    if (this.subscription) { this.subscription.unsubscribe(); }
  }

}
